<template>
    <div>
        <h3>날짜 선택</h3>
        <input type="date" :value="value" @input="updateDate" />
    </div>
</template>

<script>
export default {
    name: "DatePickerComponent",
    props: {
        value: {
            type: String,
            required: false
        }
    },
    methods: {
        updateDate(e) {
            this.$emit("input", e.target.value);
        }
    },
};
</script>

<style>
h3 {
    margin-bottom: 10px;
}

button {
    margin-top: 10px;
    padding: 5px 10px;
}
</style>
