<template>
    <div>
        <h1>LogView</h1>
        <p>날짜로 로그를 조회해 보여줘야함(default: 오늘)</p>
<!--        하위 컴포넌트에 v-model 바인딩(양방향)시 내부 작동 -->
<!--        1. v-bind로 데이터를 하위 컴포넌트의 value prop에 전달 -->
<!--        2. v-on:input으로 하위 컴포넌트에서 발생한 이벤트를 데이터에 반영 -->
        <DatePicker v-model="selectedDate"/>
        <button>조회</button>
    </div>
</template>
<script>
import DatePicker from "../components/DatePicker.vue";

export default {
    name: "LogView",
    components: {
        DatePicker,
    },
    data() {
        return {
            selectedDate: ""
        }
    },
    created() {
        const today = new Date();
        const formattedDate =
            `${today.getFullYear()}-${String(today.getMonth() + 1).padStart(2, '0')}-${String(today.getDate()).padStart(2, '0')}`;
        this.selectedDate = formattedDate;
    },
    watch: {
        selectedDate(newValue, oldValue) {
            console.log(`날짜 변경: 이전 값 = ${oldValue}, 새로운 값 = ${newValue}`);
        }
    }
};
</script>
